.Technology{display:flex;min-height:100%;}
.Technology .Tech{width:50%;overflow:hidden;min-height:100%;}
.Technology .Tech img{position:relative;min-height:100%;margin-left:-20%;}
.Technology .Team{position:relative;width:50%;background:rgba(0,0,0,0.9);min-height:100%;}
.Technology .Team .CenterContent{margin-top:50%;transform:translate(0,-50%);}
.Technology .Team .CenterContent h4{color:white;}
.Technology .Team .CenterContent p{width:50%;color:beige;margin-left:calc(25% - 20px);padding:20px;}
.Technology .Team .CenterContent table{width:100%;}
.Technology .Team .CenterContent table tr{width:100%;}
.Technology .Team .CenterContent table tr td{text-align:center;width:100%;}
.Technology .Team .CenterContent table tr td button{width:20px;height:20px; background:rgba(0,0,0,0.7);border:2px solid gray;margin:5px;margin-bottom:25px;}
.Technology .Team .CenterContent table tr td button:hover{background:white;cursor:pointer;}