.Content{display:flex;margin-top:50px;}
.Content .ContentLeft{width:50%;}
.Content .ContentLeft img{height:100%;}
.Content .ContentRight{width:50%;background:rgba(0,0,0,0.9);}
.Content .ContentRight .CenterContent{margin-top:50%;transform:translate(0,-50%);}
.Content .ContentRight .CenterContent h4{color:white;}
.Content .ContentRight .CenterContent p{width:50%;color:beige;margin-left:calc(25% - 20px);padding:20px;}
.Content .ContentRight .CenterContent table{width:100%;}
.Content .ContentRight .CenterContent table tr{width:100%;}
.Content .ContentRight .CenterContent table tr td{text-align:center;width:100%;}
.Content .ContentRight .CenterContent table tr td button{width:20px;height:20px; background:rgba(0,0,0,0.7);border:2px solid gray;margin:5px;margin-bottom:25px;}
.Content .ContentRight .CenterContent table tr td button:hover{background:white;cursor:pointer;}
.active{box-shadow:0 0 15px 3px white;}