.ContactForm{background:rgba(0,0,0,0.7);height:100%;width:100%;position:fixed;z-index:15;}
.ContactForm .ContactFormContent{background:white;width:50%;margin-left:25%;margin-top:12.5%;}
.ContactForm .ContactFormContent .CloseSpan{text-align:right;}
.ContactForm .ContactFormContent .CloseSpan strong{padding:5px;cursor:pointer;}
.ContactForm .ContactFormContent .TheForm{display:block;height:350px;}
.ContactForm .ContactFormContent .TheForm input{width:80%;margin-left:3%;border:2px solid black;padding:20px;margin-top:20px;}
.ContactForm .ContactFormContent .TheForm textarea{width:80%;margin-left:3%;border:2px solid black;height:100px;padding:20px;margin-top:20px;}
.ContactForm .ContactFormContent .TheForm .TheAncre{margin-top:25px;}
.ContactForm .ContactFormContent .TheForm .TheAncre a{border:2px solid black;text-decoration:none;padding:10px;margin-top:50px;color:black;}
.ContactForm .ContactFormContent .TheForm .TheAncre a:hover{background:gray;}